import React, { useState, useRef, useEffect } from 'react'
import Button from './Button'
import { getRandomSafePosition } from '../utils/randomPosition'

const PROPOSAL_TEXT = 'Will you be my Valentine? ðŸ’Œ'
const SUCCESS_TEXT = 'Yaaay ðŸ’• I knew it!'

const ProposalCard: React.FC = () => {
  const [yesScale, setYesScale] = useState(1)
  const [showSuccess, setShowSuccess] = useState(false)
  const [noBtnPos, setNoBtnPos] = useState({ left: '50%', top: '0%' })
  const noBtnRef = useRef<HTMLButtonElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)

  // Move No button to random position on hover/click
  const moveNoButton = () => {
    if (noBtnRef.current && containerRef.current) {
      const btn = noBtnRef.current
      const rect = btn.getBoundingClientRect()
      const width = rect.width
      const height = rect.height
      const { left, top } = getRandomSafePosition(width, height)
      setNoBtnPos({ left: `${left}px`, top: `${top}px` })
      setYesScale((prev) => Math.min(prev + 0.1, 1.3)) // Incremental scale, max 1.3
    }
  }

  // Fade out proposal and show success message
  const handleYes = () => {
    setShowSuccess(true)
    setTimeout(() => {
      // Optionally trigger celebration animation here
    }, 800)
  }

  // Responsive: reset No button position on resize
  useEffect(() => {
    const handleResize = () => {
      if (noBtnRef.current) {
        const btn = noBtnRef.current
        const rect = btn.getBoundingClientRect()
        const width = rect.width
        const height = rect.height
        const { left, top } = getRandomSafePosition(width, height)
        setNoBtnPos({ left: `${left}px`, top: `${top}px` })
      }
    }
    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  return (
    <div
      ref={containerRef}
      className='proposal-container relative w-full h-full min-h-screen'>
      {!showSuccess ? (
        <div className='flex flex-col items-center fade-scale'>
          <h1 className='text-4xl md:text-5xl font-serif font-bold mb-8 text-center'>
            {PROPOSAL_TEXT}
          </h1>
          <div
            className='relative flex flex-col items-center justify-center mt-2'
            style={{ height: 120 }}>
            <Button
              className={`bg-soft-pink text-deep-rose shadow-soft hover:bg-blush focus:bg-blush transition-all mb-4`}
              style={{ transform: `scale(${yesScale})`, zIndex: 2 }}
              onClick={handleYes}>
              Yes ðŸ’–
            </Button>
            <Button
              ref={noBtnRef}
              className='bg-blush text-deep-rose shadow-soft hover:bg-soft-pink focus:bg-soft-pink transition-all absolute'
              style={{
                left: noBtnPos.left,
                top: noBtnPos.top,
                zIndex: 3,
                transition: 'left 0.5s ease, top 0.5s ease, transform 0.3s',
              }}
              onMouseEnter={moveNoButton}
              onTouchStart={moveNoButton}
              onClick={moveNoButton}>
              No ðŸ™ˆ
            </Button>
          </div>
        </div>
      ) : (
        <div className='flex flex-col items-center justify-center fade-scale mt-16'>
          <h2 className='text-3xl md:text-4xl font-serif font-bold mb-6 text-center text-soft-pink'>
            {SUCCESS_TEXT}
          </h2>
          {/* Celebration animation placeholder: FloatingHearts or confetti */}
          <div className='relative w-full h-32'>
            {/* You can trigger FloatingHearts or confetti here */}
          </div>
        </div>
      )}
    </div>
  )
}

export default ProposalCard
